<template>
    <div class="login-register">
        <h1 class="login-register-title">Регистрация</h1>

        <form class="login-register__form" @submit.prevent="submitHandler">
            <div class="login-register__row">
                <input class="login-register__input" id="email" type="text" placeholder="Email" 
                    v-model.trim="email"
                    :class="{invalid: ($v.email.$dirty && !$v.email.required) || ($v.email.$dirty && !$v.email.email)}"
                >
                <span class="login-register__label">Email</span>

                <small class="login-register__small"
                    v-if="$v.email.$dirty && !$v.email.required"
                >
                    Укажите Email
                </small>

                <small class="login-register__small"
                    v-else-if="$v.email.$dirty && !$v.email.email"
                >
                    Введите корректный Email
                </small>
            </div>

            <div class="login-register__row">
                <input class="login-register__input" id="password" type="password" placeholder="Пароль"
                    v-model.trim="password"
                    :class="{invalid: ($v.password.$dirty && !$v.password.required) || ($v.password.$dirty && !$v.password.minLength)}"
                >
                <span class="login-register__label">Password</span>

                <small class="login-register__small"
                    v-if="$v.password.$dirty && !$v.password.required"
                >
                    Укажите пароль
                </small>

                <small class="login-register__small"
                    v-else-if="$v.password.$dirty && !$v.password.minLength"
                >
                    Минимальнаяя длина пароля {{$v.password.$params.minLength.min}} символов
                </small>
            </div>

            <div class="login-register__row">
                <input class="login-register__input" id="name" type="text" placeholder="Имя"
                    v-model.trim="name"
                    :class="{invalid: ($v.name.$dirty && !$v.name.required)}"
                >
                <span class="login-register__label">Name</span>

                <small class="login-register__small"
                    v-if="$v.name.$dirty && !$v.name.required"
                >
                    Укажите имя
                </small>                
            </div>

            <button class="login-register__submit long-btn" type="submit">Зарегистрироваться</button>
        </form>

        <div class="login-register__transition">
            <span>Уже есть аккаунт? </span>
            <router-link class="login-register__link" to="/">Войти!</router-link>
        </div>
    </div>
</template>

<script>

import {email, required, minLength} from 'vuelidate/lib/validators'

export default {
    name: 'register',
    metaInfo(){
        return {
            title: this.$title('Регистрация')
        }
    },
    data: () => ({
        email: '',
        password: '',
        name: '',
    }),
    validations: {
        email: {email, required},
        password: {required, minLength: minLength(6)},
        name: {required}
    },
    methods: {
        async submitHandler(){
            if(this.$v.$invalid){
                this.$v.$touch()
                return
            }
            const formData = {
                email: this.email,
                password: this.password,
                name: this.name
            }
            try{
                await this.$store.dispatch('register', formData)
                this.$router.push('/admin')
            } catch(e) {
                console.log(e)
            }
        }
    }
}
</script>